.image-card {
	border-color: #edeef2;
	border-width: 1px;
	border-style: solid;
	border-radius: 8px;
	min-height: 440px;
	box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.06);
	height: 100%;

	img {
		max-width: 100%;
	}

	h3 {
		font-size: 20px;
		font-weight: 500;;
	}

	p {
		font-size: 18px;
	}
}
